<template>
  <div style="display: inline-flex;">
    <div
      class="button clickable"
      :class="{installed, lanMode}"
      @click="e => !loading ? $emit('click', {lanMode}) : null"
    >
      <div v-if="actionLabel" class="text">
        {{ actionLabel }}
      </div>
      <div v-if="lanMode" class="caption">
        Anticheat disabled
      </div>
    </div>
    <div
      v-if="installed"
      :key="(lanMode ? 'up' : 'down') + 'arrow'"
      class="options clickable"
      :class="{lanMode}"
      @click="toggleLan"
    >
      <i v-if="lanMode" :key="upArrow" class="fas fa-chevron-up" />
      <i v-else :key="dwnArrow" class="fas fa-chevron-down" />
    </div>
  </div>
</template>
<script>
import { mapGetters } from 'vuex'
export default {
  name: 'MainButton',
  props: {
    game: {
      type: Object,
      required: true
    },
    installed: {
      type: Boolean
    }
  },
  data () {
    return {
      lanMode: false
    }
  },
  computed: {
    ...mapGetters({
      loading: 'ui/showLoadingModal'
    }),
    actionLabel () {
      if (this.installed) {
        if (this.lanMode) return 'Play in LAN Mode'
        return 'Play'
      }
      return 'Setup'
    }
  },
  methods: {
    toggleLan () {
      this.lanMode = !this.lanMode
    }
  }
}
</script>
<style lang="stylus" scoped>
.button {
  font-family: nimbus-sans, sans-serif;
  font-size: 44px;
  text-transform: uppercase;
  display: inline-block;
  background-color: $info;
  color: white;
  padding: 8px;
  height: 60px;
  width: 300px;
  text-align: center;
  cursor: pointer;

  &.installed {
    background-color: $positive;
  }

  .caption {
    font-size 12px;
  }

  &.lanMode {
    font-size: 28px;
    padding: 6px;
    border: 2px solid alpha(white, 0.65);
    background-color: $warning;
    .text {
      margin-top: 12px;
    }
  }
}

.options {
  font-family: nimbus-sans, sans-serif;
  font-size: 32px;
  text-transform: uppercase;
  display: inline-block;
  background-color: $positive;
  color: white;
  padding: 8px;
  padding-top: 19px;
  padding-bottom: 0;
  margin-left: 2px;
  height: 57px;
  width: 34px;
  text-align: center;
  cursor: pointer;

  &.lanMode {
    font-size: 28px;
    padding: 6px;
    padding-top: 17px;
    padding-bottom: 0;
    border: 2px solid alpha(white, 0.65);
    height: 55px;
    background-color: $warning;
    .text {
      margin-top: 12px;
    }
  }
}

.text {
  margin-top: 3px;
}
</style>
