<template>
  <div class="gamePage">
    <div class="vertical-grid">
      <div class="grid">
        <div>
          <img
            v-if="game.logo"
            :src="game.logo"
            width="350px"
          >
          <div
            v-else
            class="gameTitle"
          >
            {{ gameTag }}
          </div>
        </div>
      </div>
      <div class="grid">
        <game-action-button
          :game="game"
          :installed="!!gamePath"
          @click="gameActionButton"
        />
        <div>
          <div
            v-if="gamePath"
            class="settings clickable no-backdrop-highlight"
            @click="openGameSettings"
          >
            <i class="fas fa-cog" /> Game Settings<br>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
/* eslint-disable no-undef */
import Game from 'store/models/game'
import gameActionButton from './game-action-button'
export default {
  components: { gameActionButton },
  props: {
    gameTag: {
      type: String,
      required: true
    }
  },
  data () {
    return {
      gamePath: null
    }
  },
  computed: {
    game () {
      return Game.find(this.gameTag)
    }
  },
  watch: {
    gameTag (val) {
      this.refreshGamePath()
    }
  },
  mounted () {
    this.refreshGamePath()
  },
  methods: {
    gameActionButton (data) {
      if (this.gamePath) {
        this.$store.dispatch('entities/games/play', {
          gameTag: this.game.tag,
          params: data.lanMode ? '-lan' : ''
        })
      } else this.setGameDir()
    },
    setGameDir () {
      this.$store.dispatch('entities/games/selectDir', this.game.tag).then(path => {
        this.gamePath = path || this.gamePath
      })
    },
    refreshGamePath () {
      this.gamePath = launcherGetGameFolder(this.gameTag)
    },
    openGameSettings () {
      this.$store.dispatch('ui/setPeek', `game/${this.game.tag}`)
    }
  }
}
</script>
<style lang="stylus" scoped>
* {
  overflow: hidden;
}

.gamePage {
  word-wrap: break-word;
}

.vertical-grid {
  display: grid;
  grid-template-columns: auto;
  grid-template-rows: auto 100px;
  height: calc(100vh - 116px);
}

.grid {
  display: grid;
  grid-template-columns: min-content auto;
  grid-column-gap: 16px;
}

.gameButton {
  margin: auto auto;
}
</style>
